.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-cascader-menu-enter-active,
.tu-cascader-menu-leave-active {
  transition: all 0.3s; }

.tu-cascader-menu-enter,
.tu-cascader-menu-leave-active {
  opacity: 0;
  margin-right: -180px; }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #e4ebf5; }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #8394be;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-image__inner, .tu-image__placeholder, .tu-image__error {
  width: 100%;
  height: 100%; }

.tu-image {
  position: relative;
  display: inline-block;
  overflow: hidden; }
  .tu-image__inner {
    vertical-align: top; }
    .tu-image__inner--center {
      position: relative;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: block; }
  .tu-image__placeholder {
    background: #dfe2e7; }
  .tu-image__error {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    background: #dfe2e7;
    color: #8394be;
    vertical-align: middle; }
  .tu-image__preview {
    cursor: pointer; }

.tu-image-viewer__wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

.tu-image-viewer__btn {
  position: absolute;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  opacity: 0.8;
  cursor: pointer;
  box-sizing: border-box;
  user-select: none; }

.tu-image-viewer__close {
  top: 36px;
  right: 36px;
  width: 32px;
  height: 32px;
  font-size: 14px;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.38);
  transition: background-color 0.3s; }
  .tu-image-viewer__close:hover {
    background-color: rgba(255, 255, 255, 0.5); }

.tu-image-viewer__canvas {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.tu-image-viewer__actions {
  left: 50%;
  bottom: 30px;
  transform: translateX(-50%);
  width: 282px;
  height: 38px;
  padding: 0 23px;
  background-color: #606266;
  border-color: #fff;
  border-radius: 22px; }
  .tu-image-viewer__actions__inner {
    width: 100%;
    height: 100%;
    text-align: justify;
    cursor: default;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: space-around; }
    .tu-image-viewer__actions__inner i {
      cursor: pointer;
      color: rgba(255, 255, 255, 0.5);
      transition: color 0.3s; }
      .tu-image-viewer__actions__inner i:hover {
        color: white; }

.tu-image-viewer__prev {
  top: 50%;
  transform: translateY(-50%);
  width: 32px;
  height: 32px;
  font-size: 14px;
  color: #fff;
  border-color: #fff;
  left: 40px;
  background-color: rgba(255, 255, 255, 0.38);
  transition: background-color 0.3s; }
  .tu-image-viewer__prev:hover {
    background-color: rgba(255, 255, 255, 0.5); }

.tu-image-viewer__next {
  top: 50%;
  transform: translateY(-50%);
  width: 32px;
  height: 32px;
  font-size: 14px;
  color: #fff;
  border-color: #fff;
  right: 40px;
  text-indent: 2px;
  background-color: rgba(255, 255, 255, 0.38);
  transition: background-color 0.3s; }
  .tu-image-viewer__next:hover {
    background-color: rgba(255, 255, 255, 0.5); }

.tu-image-viewer__mask {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0.5;
  background: #000; }

.viewer-fade-enter-active {
  animation: viewer-fade-in 0.3s; }

.viewer-fade-leave-active {
  animation: viewer-fade-out 0.3s; }

@keyframes viewer-fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes viewer-fade-out {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }
