.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-cascader-menu-enter-active,
.tu-cascader-menu-leave-active {
  transition: all 0.3s; }

.tu-cascader-menu-enter,
.tu-cascader-menu-leave-active {
  opacity: 0;
  margin-right: -180px; }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #e4ebf5; }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #8394be;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-tree-select {
  display: inline-flex;
  align-items: center;
  position: relative;
  width: 100%; }
  .tu-tree-select .tu-tree-select__tags > span {
    display: contents; }
  .tu-tree-select .tu-input__inner {
    cursor: pointer;
    padding-right: 35px;
    transition: none; }
  .tu-tree-select .tu-input .tu-tree-select__caret {
    color: #bec8e4;
    transition: transform 0.3s;
    transform: rotateZ(180deg);
    cursor: pointer; }
    .tu-tree-select .tu-input .tu-tree-select__caret.is-reverse {
      transform: rotateZ(0deg); }
    .tu-tree-select .tu-input .tu-tree-select__caret.is-show-close {
      font-size: 14px;
      text-align: center;
      transform: rotateZ(180deg);
      color: #bec8e4;
      transition: color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1); }
      .tu-tree-select .tu-input .tu-tree-select__caret.is-show-close:hover {
        color: #8394be; }
  .tu-tree-select .tu-input .tu-icon-reload {
    animation: spin 1s linear infinite; }
  .tu-tree-select .tu-input.is-disabled .tu-input__inner {
    cursor: not-allowed; }
  .tu-tree-select .tu-input.is-focus .tu-input__inner {
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-tree-select > .tu-input {
    display: block; }
  .tu-tree-select__input {
    border: none;
    outline: none;
    padding: 0;
    margin-left: 15px;
    color: #bec8e4;
    font-size: 14px;
    appearance: none;
    height: 28px;
    background-color: transparent;
    flex-grow: 1; }
    .tu-tree-select__input::-moz-placeholder {
      color: #bec8e4; }
    .tu-tree-select__input:-ms-input-placeholder {
      color: #bec8e4; }
    .tu-tree-select__input::placeholder {
      color: #bec8e4; }
  .tu-tree-select__close {
    cursor: pointer;
    position: absolute;
    top: 8px;
    z-index: 1000;
    right: 25px;
    color: #bec8e4;
    line-height: 18px;
    font-size: 14px; }
    .tu-tree-select__close:hover {
      color: #8394be; }
  .tu-tree-select__tags {
    display: flex;
    width: calc(100% - 30px);
    position: absolute;
    line-height: normal;
    white-space: normal;
    z-index: 1;
    top: 50%;
    transform: translateY(-50%);
    flex-wrap: wrap; }
    .tu-tree-select__tags .tu-tag {
      margin: 5px 0 5px 6px; }
      .tu-tree-select__tags .tu-tag.tu-tag--mini {
        margin-right: -4px; }
