.tu-form--label-left .tu-form-item__label {
  text-align: left; }

.tu-form--label-top .tu-form-item__label {
  float: none;
  display: inline-block;
  text-align: left;
  padding: 0; }

.tu-form--inline .tu-form-item {
  display: inline-block;
  margin-right: 10px;
  vertical-align: top; }

.tu-form--inline .tu-form-item__label {
  float: none;
  display: inline-block; }

.tu-form--inline .tu-form-item__content {
  display: inline-block;
  vertical-align: top; }

.tu-form--inline.tu-form--label-top .tu-form-item__content {
  display: block; }

.tu-form-item {
  margin-bottom: 22px; }
  .tu-form-item::before,
  .tu-form-item::after {
    display: table;
    content: ""; }
  .tu-form-item::after {
    clear: both; }
  .tu-form-item .tu-form-item {
    margin-bottom: 0; }
  .tu-form-item .tu-input__validateIcon {
    display: none; }
  .tu-form-item--large .tu-form-item__label {
    line-height: 40px; }
  .tu-form-item--large .tu-form-item__content {
    line-height: 40px; }
  .tu-form-item--large .tu-checkbox,
  .tu-form-item--large .tu-radio {
    line-height: 40px; }
  .tu-form-item--medium .tu-form-item__label {
    line-height: 32px; }
  .tu-form-item--medium .tu-form-item__content {
    line-height: 32px; }
  .tu-form-item--medium .tu-checkbox,
  .tu-form-item--medium .tu-radio {
    line-height: 32px; }
  .tu-form-item--small .tu-form-item__label {
    line-height: 28px; }
  .tu-form-item--small .tu-form-item__content {
    line-height: 28px; }
  .tu-form-item--small.tu-form-item {
    margin-bottom: 18px; }
  .tu-form-item--small .tu-form-item__error {
    padding-top: 2px; }
  .tu-form-item--small .tu-checkbox,
  .tu-form-item--small .tu-radio {
    line-height: 28px; }
  .tu-form-item__label-wrap {
    float: left; }
    .tu-form-item__label-wrap .tu-form-item__label {
      display: inline-block;
      float: none; }
  .tu-form-item__label {
    text-align: right;
    vertical-align: middle;
    float: left;
    font-size: 14px;
    color: #8394be;
    line-height: 40px;
    padding: 0 12px 0 0;
    box-sizing: border-box; }
  .tu-form-item__content {
    align-items: center;
    line-height: 40px;
    position: relative;
    font-size: 14px; }
    .tu-form-item__content::before,
    .tu-form-item__content::after {
      display: table;
      content: ""; }
    .tu-form-item__content::after {
      clear: both; }
    .tu-form-item__content .tu-input-group {
      vertical-align: top; }
  .tu-form-item__error {
    color: #f56c6c;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
    position: absolute;
    top: 100%;
    left: 0; }
    .tu-form-item__error--inline {
      position: relative;
      top: auto;
      left: auto;
      display: inline-block;
      margin-left: 10px; }
  .tu-form-item.is-required:not(.is-no-asterisk) > .tu-form-item__label:before,
  .tu-form-item.is-required:not(.is-no-asterisk) .tu-form-item__label-wrap > .tu-form-item__label:before {
    content: "*";
    color: #f56c6c;
    margin-right: 4px; }
  .tu-form-item.is-error .tu-input__inner,
  .tu-form-item.is-error .tu-textarea__inner {
    position: relative; }
  .tu-form-item--feedback .tu-input__validateIcon {
    display: inline-block; }
