.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-cascader-menu-enter-active,
.tu-cascader-menu-leave-active {
  transition: all 0.3s; }

.tu-cascader-menu-enter,
.tu-cascader-menu-leave-active {
  opacity: 0;
  margin-right: -180px; }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #e4ebf5; }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #8394be;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-message-box {
  display: inline-block;
  width: 420px;
  padding-bottom: 10px;
  vertical-align: middle;
  border-radius: 4px;
  border: 1px solid rgba(190, 200, 228, 0.18);
  font-size: 14px;
  text-align: left;
  overflow: hidden;
  backface-visibility: hidden;
  background-color: #e4ebf5; }
  .tu-message-box__wrapper {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center; }
    .tu-message-box__wrapper::after {
      content: "";
      display: inline-block;
      height: 100%;
      width: 0;
      vertical-align: middle; }
  .tu-message-box__header {
    position: relative;
    padding: 12px 18px; }
  .tu-message-box__title {
    padding-left: 0;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 500;
    line-height: 1;
    color: #8394be; }
  .tu-message-box__headerbtn {
    position: absolute;
    top: 12px;
    right: 18px;
    padding: 0;
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    cursor: pointer; }
    .tu-message-box__headerbtn .tu-message-box__close {
      color: #8394be; }
    .tu-message-box__headerbtn:focus .tu-message-box__close, .tu-message-box__headerbtn:hover .tu-message-box__close {
      color: #6d5dfc; }
  .tu-message-box__content {
    padding: 10px 18px;
    color: #9baacf;
    font-size: 14px; }
    .tu-message-box__content div,
    .tu-message-box__content p,
    .tu-message-box__content span {
      color: #9baacf; }
    .tu-message-box__content .tu-message-box__errormsg {
      color: #f56c6c; }
  .tu-message-box__container {
    position: relative; }
  .tu-message-box__input {
    padding-top: 15px; }
    .tu-message-box__input input.invalid {
      border-color: #f56c6c; }
      .tu-message-box__input input.invalid:focus {
        border-color: #f56c6c; }
  .tu-message-box__status {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 14px; }
    .tu-message-box__status::before {
      padding-left: 1px; }
    .tu-message-box__status + .tu-message-box__message {
      padding-left: 24px;
      padding-right: 12px; }
    .tu-message-box__status.tu-icon__success {
      color: #6bc43f; }
    .tu-message-box__status.tu-icon__info {
      color: #9baacf; }
    .tu-message-box__status.tu-icon__warning {
      color: #e6a23c; }
    .tu-message-box__status.tu-icon__error {
      color: #f56c6c; }
  .tu-message-box__message {
    margin: 0; }
    .tu-message-box__message p {
      margin: 0;
      line-height: 24px; }
  .tu-message-box__errormsg {
    color: #f56c6c;
    font-size: 12px;
    min-height: 18px;
    margin-top: 6px; }
  .tu-message-box__btns {
    padding: 5px 15px 0;
    text-align: right; }
    .tu-message-box__btns button:nth-child(2) {
      margin-left: 10px; }
  .tu-message-box__btns-reverse {
    flex-direction: row-reverse; }
  .tu-message-box--center {
    padding-bottom: 18px; }
    .tu-message-box--center .tu-message-box__header {
      padding-top: 18px; }
    .tu-message-box--center .tu-message-box__title {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center; }
    .tu-message-box--center .tu-message-box__status {
      position: relative;
      top: auto;
      padding-right: 5px;
      text-align: center;
      transform: translateY(-1px); }
    .tu-message-box--center .tu-message-box__message {
      margin-left: 0; }
    .tu-message-box--center .tu-message-box__btns, .tu-message-box--center .tu-message-box__content {
      text-align: center; }
    .tu-message-box--center .tu-message-box__content {
      padding-left: 28px;
      padding-right: 28px; }

.msgbox-fade-enter-active {
  animation: msgbox-fade-in 0.3s; }

.msgbox-fade-leave-active {
  animation: msgbox-fade-out 0.3s; }

@keyframes msgbox-fade-in {
  0% {
    transform: translate3d(0, -20px, 0);
    opacity: 0; }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }

@keyframes msgbox-fade-out {
  0% {
    transform: translate3d(0, 0, 0);
    opacity: 1; }
  100% {
    transform: translate3d(0, -20px, 0);
    opacity: 0; } }
